@font-face {
    font-family: VerminVibes2soft;
    src: url("../../Fonts/VerminVibes2soft.otf") format("truetype");
}
.title_container_table{
    display: flex;
    justify-content: center;
    @media screen and(min-width: 350px){
        font-size: 20px;
    }
    @media screen and(min-width: 450px){
        font-size: 30px;
    }
    @media screen and(min-width: 750px){
        font-size: 170px;
    }
    .title_table {
        font-family: VerminVibes2soft;
        letter-spacing: 2px;
        padding:20px;
        color:#386a82;
        justify-content: center;
        @media screen and(min-width: 350px){
            font-size: 22px;
        }
        @media screen and(min-width: 400px){
            font-size: 24px;
        }
        @media screen and(min-width: 750px){
            font-size: 30px;
        }

        }
    }





